.login {
    background: #F9F9FE;
    width: 413px;
    padding: 25px 33px 26px 33px;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 8px;
}

.subtitle {
    font-family: Poppins;
    font-weight: 600;
    font-size: 22px;
    line-height: 33px;
    color: #2D2E46;
}

.formBox {
    display: flex;
    flex-direction: column;
    margin-top: 38px;
}

