.linkTransparent {
    width: 139px;
    height: 40px;
    font-family: "SFUIDisplay-Regular";
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #2D2E46;
    opacity: 0.5;
    /* border: 1px solid #F9F9FE;
            ; */
    text-align: center;
    outline: none;
    text-decoration: none;
    background-color: inherit;
    margin-left: 210px;
    margin-bottom: 80px;
    cursor: pointer;
}

.btnBlue {
    background: #21268F;
    text-align: center;
    letter-spacing: 0.01em;
    color: #ECECF9;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
    border-radius: 30px;
    width: 266px;
    height: 36px;
    font-family: "SFUIDisplay-Medium";
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    margin: 0 auto;
    border: none;
    cursor: pointer;
    margin-bottom: 30px;
    box-shadow: 0px 4px 18px rgba(33, 38, 143, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
}

.btnBlue:disabled{
    opacity: 0.6;
}


.linkWrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textLight {
    text-align: center;
    color: #2D2E46;
    opacity: 0.5;
    font-family: "SFUIDisplay-Semibold";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    text-decoration: none;
    margin-bottom: 7px;
}

.linkBlue {
    text-align: center;
    color: #21268F;
    font-family: "SFUIDisplay-Semibold";
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-decoration: none;
}