li:first-child button {
    width: 12px;
    height: 12px;
    background: url(./../../../assets/images/rowPaginationLeft.svg) 3px 0 no-repeat;
    border:none;
    color: transparent;
    cursor: pointer;

}
li:last-child button {
    width: 12px;
    height: 12px;
    background: url(./../../../assets/images/rowPaginationRight.svg) 3px 0 no-repeat;
    border:none;
    color: transparent;
    cursor: pointer;

}