.signUp {
    background: #F9F9FE;
    width: 413px;
    padding: 25px 33px 60px 33px;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 8px;
}


